import React from 'react';
import Carousel from 'react-material-ui-carousel';
import { Item } from './Item';
function Example(props) {
  var items = [
    {
      id: 1,
      image:
        'https://www.hdwallpaper.nu/wp-content/uploads/2017/02/fitness-17.jpg',
    },
    {
      id: 2,
      image:
        'https://i.pinimg.com/originals/d7/63/c5/d763c500dacabcf80ff85212938b0e7c.jpg',
    },
    {
      id: 3,
      image:
        'https://th.bing.com/th/id/OIP.GUY2ecETsdblNDRJ3uGwMgHaE8?w=237&h=180&c=7&r=0&o=5&pid=1.7',
    },
  ];

  return (
    <Carousel sx={{ marginTop: '10px' }}>
      {items.map((item, i) => (
        <Item key={i} item={item} />
      ))}
    </Carousel>
  );
}

export default Example;
